var tha,js;
export default {
	onLoad(){
		tha = this;
		
	},
	onUnload(){
		clearInterval(js)
		this.second = 0;
	},
	data() {
		return {
			phoneno:'',
			password:'',
			code:'',
			invitation:'',
			xieyi:true,
			showPassword:false,
			second:0
		};
	},
	computed:{
		yanzhengma(){
			if(this.second==0){
				return '获取验证码';
			}else{
				if(this.second<10){
					return '重新获取0'+this.second;
				}else{
					return '重新获取'+this.second;
				}
			}
		}
	},
	methods: {
		display() {
		    this.showPassword = !this.showPassword
		},
		xieyitong(){
			this.xieyi = !this.xieyi;
		},
		getcode(){
			if(this.second>0){
				return;
			}
			this.second = 60;
			uni.request({
			    url: 'http://***/getcode.html', //仅为示例，并非真实接口地址。
			    data: {phoneno:this.phoneno,code_type:'reg'},
				method: 'POST',
				dataType:'json',
			    success: (res) => {
					if(res.data.code!=200){
						uni.showToast({title:res.data.msg,icon:'none'});
					}else{
						uni.showToast({title:res.data.msg});
						js = setInterval(function(){
							tha.second--;
							if(tha.second==0){
								clearInterval(js)
							}
						},1000)
					}
			    }
			});
		},
	    bindLogin() {
			if (this.xieyi == false) {
			    uni.showToast({
			        icon: 'none',
			        title: '请先阅读《软件用户协议》'
			    });
			    return;
			}
			if (this.phoneno.length !=11) {
			    uni.showToast({
			        icon: 'none',
			        title: '手机号不正确'
			    });
			    return;
			}
	        if (this.password.length < 6) {
	            uni.showToast({
	                icon: 'none',
	                title: '密码不正确'
	            });
	            return;
	        }
			if (this.code.length != 4) {
			    uni.showToast({
			        icon: 'none',
			        title: '验证码不正确'
			    });
			    return;
			}
			uni.request({
			    url: 'http://***/reg.html',
			    data: {
					phoneno:this.phoneno,
					password:this.password,
					code:this.code,
					invitation:this.invitation
				},
				method: 'POST',
				dataType:'json',
			    success: (res) => {
					if(res.data.code!=200){
						uni.showToast({title:res.data.msg,icon:'none'});
					}else{
						uni.showToast({title:res.data.msg});
						setTimeout(function(){
							uni.navigateBack();
						},1500) 
					}
			    }
			});
			
	    }
	}
}
